/* unplugin-vue-components disabled */import HeadBar from '../components/HeadBar.vue';
import api from '../api/index';
export default {
  name: 'RegisterView',

  data() {
    return {
      // displayFlag : false,
      // displayDeskOnly: true,
      iswarning: false,
      errorMessage: "",
      warningText: "",
      email: "",
      code: "",
      pwd: "",
      pwdRepeat: "",
      headerStyle: "Return"
    };
  },

  components: {
    HeadBar
  },
  methods: {
    verifyButton() {
      this.verifyCode();
    },

    verifyCode() {
      var that = this;
      const loading = this.openFullScreen2();
      api.emailVerify(that.userName, that.email).then(function (response) {
        loading.close();

        if (response.data.status == 200) {
          console.log(response.data);
          ElNotification({
            message: 'Verify code send out',
            type: 'success',
            duration: 5000
          });
        } else {
          ElNotification({
            message: 'Invalid email',
            type: 'error',
            duration: 5000
          });
        }
      });
    },

    backToPrev() {
      this.$router.back(-1);
    },

    confirmButton() {
      this.isEmpty();
    },

    resetPassword() {
      var that = this;

      if (that.email.length < 1 || that.code.length < 1 || that.pwd.length < 1 || that.pwdRepeat.length < 1) {
        ElNotification({
          message: 'You must complete all information!',
          type: 'warning',
          duration: 5000
        });
      } else if (that.pwd.length <= 7) {
        ElNotification({
          message: 'The password must be at least 8 characters!',
          type: 'warning',
          duration: 5000
        });
      } else if (that.pwd != that.pwdRepeat) {
        ElNotification({
          message: 'The two password are inconsistent!',
          type: 'warning',
          duration: 5000
        });
      } else {
        const loading = this.openFullScreen2();
        api.resetPassward(that.email, that.code, that.pwd).then(function (response) {
          loading.close();

          if (response.data.status == 200) {
            that.$router.push({
              path: '/'
            });
            ElNotification({
              message: 'Success!',
              type: 'success',
              duration: 5000
            });
          } else {
            that.errorMessage = response.data.msg;
            ElNotification({
              message: that.errorMessage,
              type: 'error',
              duration: 5000
            });
          }
        });
      }
    },

    openFullScreen2() {
      const loading = ElLoading.service({
        lock: true,
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.7)'
      }); // setTimeout(() => {
      //   loading.close()
      // }, 10000)

      return loading;
    }

  },

  mounted() {
    this.displayDeskOnly = window.innerWidth > 992;
  }

};